<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="'false'" ModuleName="Dynamic_Reporting" SubModuleCode="Dynamic_Reporting" IdentityColumn="Report_emailId"
            HeaderText="Scheduled Report History" :ListData="HistoryData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :IsShowCheckbox="'false'"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick">
            <template v-slot:slotdata="{ data }">
            <template v-if="data.column.COLUMN_NAME == 'Status'">
                <span title="" v-if="data.row.Status == 'PENDING' ||  data.row.Status == 'Pending'" class="px-2 bg-secondary text-center text-white d-block">{{data.row.Status }}</span>
                <span title="" v-else-if="data.row.Status == 'DONE' || data.row.Status == 'Done'" class="px-2 bg-success text-center text-white d-block">{{ data.row.Status }}</span>
                <span title="" v-else-if="data.row.Status == 'IN PROGRESS' || data.row.Status == 'In Progress'" class="px-2 bg-warning text-center text-white d-block">{{ data.row.Status }}</span>
                <span title="" v-else class="px-2 text-center text-black d-block"> {{ data.row.Status }}</span>
            </template>
            <template>
                 <a v-if="data.column.COLUMN_NAME == 'Download'"
                    :href="data.row.Download" download :title="data.row.Download"> 
                    <em :class="{'fa fa-download action-icon text-success ml-5':true,'disabled':data.row.Download==null || data.row.Download==''}"></em>
                </a>
            </template>
        </template>
        </tg-list>
    </section>
</template>

<script>
import DataService from "@/services/DataService";
import Schema from "@/views/Reporting/ReportScheduledHistory/HistoryListingSchema";

export default{
    data(){
        return{
            isLoading:false,
            HistoryData:[],
            listheaderbuttons: Schema.buttons,
            leftSearchFields:Schema.leftsearchSchema,
            TotalRecords:0,
            SortBy:'',
            SortExp:'',
            PageNumber:1,
            PageSize:10,
            searchCondition:'',
             Headers: [
                {
                    COLUMN_NAME: "ReportName",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Report Name",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "Req_From",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Request From",
                    DISPLAY_ORDER: 2,
                },
                {
                    COLUMN_NAME: "Send_To",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Send To",
                    DISPLAY_ORDER: 3,
                },
                {
                    COLUMN_NAME: "Created_by",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Created By",
                    DISPLAY_ORDER: 4,
                },
                {
                    COLUMN_NAME: "Created_on",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Created At",
                    DISPLAY_ORDER:5,
                },
                {
                    COLUMN_NAME: "Status",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Status",
                    DISPLAY_ORDER: 6,
                },
                {
                    COLUMN_NAME: "Download",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Download",
                    DISPLAY_ORDER: 7,
                }
            ],

        }
    },
    created: async function (){
        var vm=this;
        vm.Headers.forEach((column) => {
            column.settings = null;
            if (column.COLUMN_NAME == "Status" || column.COLUMN_NAME == "Download") {
                column.settings = {
                    isInSlot: true,
                };
            }
        });
        await vm.FetchData();
    },
    methods:{
        FetchData: async function(){
            var vm =this;
            vm.isLoading = true;

            const params = vm.getRequestParams(this.pageNumber, this.pageSize, this.sortBy, this.sortExp, this.isPaged, this.searchCondition);
            params.pageSize = vm.PageSize;
            params.pageNumber = vm.PageNumber;
            if (vm.searchCondition != '') {
                params.ReportName = $(".search").val();
            }
            if (vm.PageNumber) { params.pageNumber = vm.PageNumber; }
            else { vm.PageNumber = params.pageNumber = 1; }

            await DataService.GetScheduleReportHistoryListing(params)
                .then(function (response) {
                    if(response.data.Data.length > 0 && response.data.Data != []){
                        vm.HistoryData = response.data.Data;
                        vm.TotalRecords = response.data.Data[0].Total_Record}
                    else{
                        vm.HistoryData=[];
                        vm.TotalRecords=0;
                    }
                    vm.isLoading = false;
                },function(error){
                    console.log("Error during API hitting: ",error.message)
                    vm.TotalRecords = 0;
                    vm.isLoading = false;
                });
                setTimeout(function () {
                    vm.ResponsiveDataTable('tablelistingdata')
                }, 500);
        },
        leftCommonSearch:async function (value) {
            var vm=this;
            vm.PageNumber = value.PageNumber;
            vm.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                vm.searchCondition = value.searchCondition;
            }
            else {
                vm.searchCondition = "";
            }
            await vm.FetchData();
        },
        pagerMethod:async function (value) {
            var vm=this;
            vm.PageSize = value.PageSize;
            vm.PageNumber = value.PageNumber;
            await vm.FetchData();
        },
        commonHeaderButtonClick: async function (event) {
            var vm =this;
            switch (event.callbackfunction) {
                case "BackToMainList":
                    vm.BackToMainList();
                    break;
            }
        },
        BackToMainList:function(){
            var vm = this;
            vm.$router.push({ name: 'EmailScheduleListing' })
        },
        getRequestParams: function (pageNumber, pageSize, sortBy, sortExp, isPaged, searchcondition) {
            let params = {};
            if (pageNumber) { params["pageNumber"] = pageNumber; }
            if (searchcondition) { params["searchcondition"] = searchcondition; }
            if (isPaged) { params["is_paged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        }
    }
}
</script>